import Vue from 'vue';
let qs = require('qs');

export default {
    queryByPage(param){
        return Vue.axios.post( '/user/node/list',param);
    },
    bindbatch(param){
        return Vue.axios.post( '/user/node/bindbatch/',param);
    },
    unbindbatch(param){
        return Vue.axios.post( '/user/node/unbind/',param);
    },
    join(param){
        return Vue.axios.post( '/user/node/share/join',param);
    },
    quit(param){
        return Vue.axios.post( '/user/node/share/quit',param);
    },

}


